.Simple__switch {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    white-space: pre;
}

.Simple__switch p, .Simple__switch a {
    color: inherit;
    text-decoration: none;
    outline: none;
    font-size: 1.5rem;
}

.Choices__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.Choices__text {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.Simple__switch .a__disabled:hover, .Simple__switch .a__disabled:focus {
    opacity: 1;
}

.a__enabled {
}

.a__disabled {
    opacity: 0.5;
}

.Choices__line__wrapper {
    position: relative;

    width: 100%;
    height: fit-content;
}

.Choices__line {
    position: absolute;
    top: -0.3rem;
    left: 0;

    height: 2px;
    width: 15px;

    background: black;

    transition: 0.5s cubic-bezier(.56,.46,.44,.97);
}

@media (max-width: 768px) {
    .Simple__switch {
        flex-wrap: wrap;
        justify-content: start;
    }

    .Simple__switch p, .Simple__switch a {
        font-size: 1.2rem;
    }
}